import React, { Component } from 'react';
import Welcome from './numshadow';

// var firstBS = true,
// secondBS = true;

class Boxshadow extends Component {

  constructor(props){
    super(props);
    this.state = {num:[0], check:false};
    this.globl = React.createRef();
  }
  delNum(){
    var arry = this.state.num;
    arry.pop();
    // aryShadow.pop();
    this.setState({num: arry});

  }
  addNum(){
    var arry = this.state.num;
    arry.push(arry.length);
    // aryShadow.push('0 0 0 0 black');
    this.setState({num: arry});

  }
  opens(){
    this.setState({check: !this.state.check});
  }


  render() {

    const listShadow = !this.state.check?null:this.state.num.map((i) =>
            <Welcome key={i} index={i} rang={0} status={this.props.status} tun={this.props.tun} />),
          button = !this.state.check?null:<div><button onClick={this.addNum.bind(this)}>Добавить</button><button onClick={this.delNum.bind(this)}>Удалить</button></div>,
          check = this.state.check,
          class1 = this.state.check?'statusBarCheck':'statusBar';

    return (
      <div>
        <label className={class1}><input type="checkbox" className="checSection" onChange={this.opens.bind(this)} checked={this.state.check} />BOX-SHADOW</label>
        {listShadow}
        {button}
      </div>
    );
  }
}

export default Boxshadow;



