import React from 'react';

class Welcome extends React.Component {
  constructor(props){
    super(props);


    //this.react = this.react.bind(this);
    //this.reactex = this.reactex.bind(this);
  }

  shadow(e) {
    console.log(this);
    const short = this.props.tun.state.valueElem,
          shadowItem = short[this.props.status]['box-s']!=undefined?short[this.props.status]['box-s']:[['0 0 0 0 #000',[0,0,0,0,'#000','',false]]],
          arr = shadowItem[this.props.index]==undefined?['0 0 0 0 #000',[0,0,0,0,'#000','',false]]:shadowItem[this.props.index]; 
    console.log(shadowItem[this.props.index]);
    console.log(arr);
    if(e.target.getAttribute('data-s') === '5'){
      console.log(e.target.checked);
      arr[1][6] = e.target.checked;
      arr[1][5] = e.target.checked?' inset':'';
    }else{
      arr[1][e.target.getAttribute('data-s')] = e.target.value;
    }
    const text = arr[1][0]+'px '+arr[1][1]+'px '+arr[1][2]+'px '+arr[1][3]+'px '+arr[1][4]+arr[1][5];
    arr[0] = text

    shadowItem[this.props.index] = arr;
    var newArray = shadowItem.map(function(name, index) {
      return shadowItem[index][0];
    });
    console.log(newArray);
//siz, this.props.status, 'valBG', arr
    this.props.tun.funcThis('box-shadow', newArray.join(', '), this.props.status, 'box-s', shadowItem);
   // blockStandart.style.boxShadow = aryShadow.join(', ');


  //  this.style();
  }
//
   render() {

    const short = this.props.tun.state.valueElem, //.s1["box-s0"]
          val = short[this.props.status]['box-s']!=undefined?short[this.props.status]['box-s']:[[[''],[0,0,0,0,'#000','',false]]],
          valinp = val[this.props.index]==undefined?[0,0,0,0,'#000','',false]:val[this.props.index][1],
          classInset = valinp[6]?'menu-r-angle-Check miniS-Font':'menu-r-angle miniS-Font';
    console.log(valinp);
    return (
    <div className="block" ref={this.bazic}>
      <h3 className="spanFont">SHADOW {this.props.index+1}</h3>
      <br />
      <article className="dtfull">
        <div className="t-td">
          <span>Сдвиг по X</span><br />
          <input type="range"  data-s="0" onChange={this.shadow.bind(this)} min="-100" max="100" value={valinp[0]} />
          <input className="inputTextStyle" data-s="0" onChange={this.shadow.bind(this)} type="text" value={valinp[0]} />
          <br />
          <span>Размытие</span><br />
          <input type="range" data-s="2" onChange={this.shadow.bind(this)} min="0" max="100" value={valinp[2]} />    
          <input className="inputTextStyle" data-s="2" onChange={this.shadow.bind(this)} type="text" value={valinp[2]} />
        </div>
        <div  className="t-td">  
          <span>Сдвиг по Y</span><br />
          <input type="range" data-s="1" onChange={this.shadow.bind(this)} min="-100" max="100" value={valinp[1]} /> 
          <input className="inputTextStyle" data-s="1" onChange={this.shadow.bind(this)} type="text" value={valinp[1]} />
          <br />
          <span>Растяжение</span><br />  
          <input type="range" data-s="3" onChange={this.shadow.bind(this)} min="-100" max="100" value={valinp[3]} />
          <input className="inputTextStyle" data-s="3" onChange={this.shadow.bind(this)} type="text" value={valinp[3]} />
        </div> 
      </article>  
    <input type="color" onClick={this.shadow.bind(this)} data-s="4" onChange={this.shadow.bind(this)} value={valinp[4]} />
    <br /><br />
    <label className={classInset}><input data-s="5" onChange={this.shadow.bind(this)} className="checSection" type="checkbox" checked={valinp[6]} />INSET</label>
      <hr />
    </div>

);}
}
export default Welcome



