import React, { Component } from 'react';
import Boxshadow from './boxshadow';
import BigBlock from './bigBlock';
import './brReact.css';

class State extends Component {

  constructor(props){
    super(props);
    this.state = {status:[true,false,false]};

  }

  checkedIn(e){

    const index = e.target.getAttribute('data-index');
    this.props.rara('s'+index);

    const arr =[false,false,false];
    arr[e.target.getAttribute('data-index')-1] = true;
    this.setState(function(state) {
      return {
        status: arr
      };
    });
  }

  render() {
    const arrCheck = [];
          arrCheck[0] = this.state.status[0]?'statusCheck status-font':'status status-font';
          arrCheck[1] = this.state.status[1]?'statusCheck status-font':'status status-font';
          arrCheck[2] = this.state.status[2]?'statusCheck status-font':'status status-font';

    return (
      <div className="dtfull">
  				<div className="dinlineitem qty">
  					<label className={arrCheck[0]}><input className="checSection" type="radio" data-index="1" name="stylet" onChange={this.checkedIn.bind(this)} checked={this.state.stand} />STANDART</label>
  				</div>
  				<div className="dinlineitem qty">
  					<label className={arrCheck[1]}><input className="checSection" type="radio" data-index="2" name="stylet" onChange={this.checkedIn.bind(this)} checked={this.state.hover} />HOVER</label>
  				</div>
  				<div className="dinlineitem qty">
  					<label className={arrCheck[2]}><input className="checSection" type="radio" data-index="3" name="stylet" onChange={this.checkedIn.bind(this)} checked={this.state.activ} />ACTIVE</label>
  				</div>
  		</div>
    );
  }
}

export default State;



