import React, { Component } from 'react';
import Boxshadow from './boxshadow';
import ButtonT from './bigBlock';
import BigBlock from './bigBlock';
import State from './state';
import './brReact.css';

class SizeButton extends Component {

  constructor(props){
    super(props);
    this.statwidth = React.createRef();
    this.state = {dor: false};
    this.element = '';

  }

   opens(){
    var dor = this.state.dor;
    this.setState(function(state) {
      return {
        dor: !dor,
      };
    });
   }

  
  render() {
    const class1 = this.state.dor?'statusBarCheck':'statusBar';
    return (
      <article ref={this.statwidth}>
        <label className={class1}><input type="checkbox" className="checSection" onChange={this.opens.bind(this)} /><span className="font-Big-Button">SIZE</span></label>
        <OpenBlock dor={this.state.dor} func={this.props.tun} status={this.props.status} />
      </article>
    );
  }
}

export default SizeButton;

class OpenBlock extends Component {
  constructor(props){
    super(props);
    this.checkInp = [true, false, false];
  //  console.log(this.props.elem);
  }

  widthFuncW(e){
    if(this.props.func.buttest.current != null){
      this.props.func.funcThis('width', e.target.value+'px', this.props.status, 'width', Number(e.target.value));    
    }
  }
  heighFuncH(e){
    if(this.props.func.buttest.current != null){
      this.props.func.funcThis('height', e.target.value+'px', this.props.status, 'height', Number(e.target.value));
    }
  }
  checkedCol(e){
    
    const numBlock = e.target.getAttribute('data-index');
    const arrBgBlock = [false, false, false];
    arrBgBlock[numBlock] = true;

    
    this.props.func.funcThis('none', 'bgcb', this.props.status, arrBgBlock);
  }

  render(){
      const size = this.props.func.state.valueElem[this.props.status];
      const default1 = size['width'] != undefined? size['width']:60,
            default2 = size['height'] != undefined? size['height']:23;
      const numOpens = size['bgcb'] != undefined?size['bgcb']:[true, false, false],
            chek0 = numOpens[0],
            chek1 = numOpens[1],
            chek2 = numOpens[2],
            classN1 = numOpens[0]? 'statusCheck mini-Font':'status mini-Font',
            classN2 = numOpens[1]? 'statusCheck mini-Font':'status mini-Font',
            classN3 = numOpens[2]? 'statusCheck mini-Font':'status mini-Font';
    //console.log(this.props.func.funcThis);
    if(this.props.dor){

      return(
        <div>
          <div className="size-item">
            <h2>WIDTH</h2>

            <input type="range" min='60' max='300' value={default1}  onChange={this.widthFuncW.bind(this)} />
            <input className="inputTextStyle" type="text" value={default1} onChange={this.widthFuncW.bind(this)}/>

            <h2>HEIGHT</h2>

            <input type="range" min='23' max='300' value={default2}  onChange={this.heighFuncH.bind(this)} />
            <input className="inputTextStyle" type="text" value={default2} onChange={this.heighFuncH.bind(this)}/>
          </div>  
          <div className="dtfull">
            <div className="dinlineitem qty">
              <label className={classN1}><input type="radio" className="checSection" data-index={'0'} name="colorButton" onChange={this.checkedCol.bind(this)} checked={chek0} />COLOR</label>
            </div> 
            <div className="dinlineitem qty">
              <label className={classN2}><input type="radio" className="checSection" data-index={'1'} name="colorButton" onChange={this.checkedCol.bind(this)} checked={chek1} />GRADIENT</label>
            </div> 
            <div className="dinlineitem qty">
              <label className={classN3}><input type="radio" className="checSection" data-index={'2'} name="colorButton" onChange={this.checkedCol.bind(this)} checked={chek2} />IMAGE</label>
            </div>  
          </div> 
          <ColorBlock checkNum={numOpens} status={this.props.status} func={this.props.func} />
        </div>
      );
    }else{
    return(
      null
      );
    }
    
  }
}

class ColorBlock extends Component {

  constructor(props){
    super(props);
    //this.state = {color: {s1:'#000000', s2:'#000000', s3:'#000000', render:'1'}}
    
  }
  colorAdd(e){
    console.log(e.target.value);
    var obj = e.target.value;
   // console.log(obj[this.props.status]+', '+this._reactInternalFiber.child.child.stateNode.value);
    this.props.func.funcThis('background', obj, this.props.status, 'bgColor' , obj);
    // this.setState(function(state) {
    //   return {
    //    color: obj
    //   };
    // });
  }

  render(){
    console.log(this.props);
    const color = this.props.func.state.valueElem[this.props.status];
    const default1 = color['bgColor'] != undefined? color['bgColor']:'#000000';
    if(this.props.checkNum[0]){
        return(
          <div className="size-item"><input type="color" Value={default1} onClick={this.colorAdd.bind(this)} onChange={this.colorAdd.bind(this)} /></div>
        );} else if(this.props.checkNum[1]){
          return(
          <Gradient status={this.props.status} func={this.props.func} />
          
        );}else if(this.props.checkNum[2]){
          return(
          
            <Url status={this.props.status} func={this.props.func} />
          
        );}
  }
}

class Gradient extends Component {
  constructor(props){
    super(props);
    this.state = {quantity: [0,0], arryColor: {s1:[['#000000', '#ffffff'],['0', '100'],['0','0'],'180'],s2:[['#000000', '#ffffff'],['0', '100'],['0','0'],'180'],s3:[['#000000', '#ffffff'],['0', '100'],['0','0'],'180']}}
    this.grad = React.createRef();
    this.trueGradient = true;
    this.megoArr = this.state.arryColor;
  }
  add(){
    console.log(this.props.func.state.valueElem[this.props.status]);
    const reduc = this.props.func.state.valueElem[this.props.status];    
    const colorList = reduc['colorGradient']!=undefined? reduc:{colorGradient:{color:['#ffffff','#000000'],range:['0','100'],deg:['180']}};
          //colorList['colorGradient'] = colorList['colorGradient']['color']!=undefined? colorList['colorGradient']['color']:{}

        // colorList = ['#ffffff', '#000000']; //['color'] = ['#ffffff', '#000000'];['colorGradient']
        console.log(colorList['color']);
      //  console.log(colorList.colorGradient.color);
        colorList['colorGradient']['color'].push('#000000');
        colorList['colorGradient']['range'].push('100');
   // console.log(colorList);
    this.props.func.funcThis('none', 'colorGradient', this.props.status, colorList['colorGradient'])
    
  }
  dell(){
    const reduc = this.props.func.state.valueElem[this.props.status];    
    const colorList = reduc['colorGradient']!=undefined? reduc:{colorGradient:{color:['#ffffff','#000000'],range:['0','100'],deg:['180']}};
    if(colorList['colorGradient']['color'].length>2){
      colorList['colorGradient']['color'].pop();
      colorList['colorGradient']['range'].pop();
    }
      this.props.func.funcThis('none', 'colorGradient', this.props.status, colorList['colorGradient'])
    
  }
  degFunc(e){
    const reducs = this.props.func.state.valueElem[this.props.status];    
    const degList = reducs['colorGradient']!=undefined? reducs:{colorGradient:{color:['#ffffff','#000000'],range:['0','100'],deg:['180']}};
    degList['colorGradient']['deg'] = e.target.value;

    this.props.func.funcThis('none', 'colorGradient', this.props.status, degList['colorGradient']);
    this.painting(e);
  }
  painting(e){
    const reducs = this.props.func.state.valueElem[this.props.status];    
    const colorList = reducs['colorGradient']!=undefined? reducs:{colorGradient:{color:['#ffffff','#000000'],range:['0','100'],deg:['180']}};

    var test = e !== undefined? e.target.getAttribute('data-indexR'):'deg';
    if(test !== 'deg'){
      const indexer = e.target.getAttribute('data-indexR');
      console.log(e.target.type);
      const objIndex = e.target.type === 'color'?'color':'range';
      colorList['colorGradient'][objIndex][indexer] = e.target.value;
    }

    var arryDubl = colorList;
    var arryPaint = colorList['colorGradient']['color'];
    var arryPaint2 = colorList['colorGradient']['range'];
    console.log(colorList);
    const reduc = this.props.func.state.valueElem,
    variable = reduc[this.props.status]['radioGradient'] != undefined? reduc[this.props.status]['radioGradient']:[true,false,false];
    var pircent = variable[2]?'px':'%';
    var newArray = arryPaint.map(function(name, index) {
      return arryPaint[index]+' '+arryPaint2[index]+pircent;
    });
    


    //arryDubl.join(', ')
    console.log(this.props.func.state.valueElem);
    if(variable[0]){
       this.props.func.funcThis('background', 'linear-gradient('+colorList['colorGradient']['deg']+'deg, '+newArray.join(', ')+')', this.props.status,'colorGradient', colorList['colorGradient']);
    }else if(variable[2]){
       this.props.func.funcThis('background', 'repeating-linear-gradient('+colorList['colorGradient']['deg']+'deg, '+newArray.join(', ')+')', this.props.status,'colorGradient', colorList['colorGradient']);
    }else{
      this.props.func.funcThis('background', 'radial-gradient('+newArray.join(', ')+')',this.props.status,'colorGradient', colorList['colorGradient']);
    }
    
  }
  gradient(e){

     console.log(e.target.getAttribute('data-index'));
     const numButton = e.target.getAttribute('data-index'),
     arrCheked = [false, false, false];
     arrCheked[numButton] = true;
     this.props.func.funcThis('none', 'radioGradient', this.props.status, arrCheked);
     this.painting();
    // this.trueGradient = true;
    
    // var obj = this.state.radioGradients;
    // obj[this.props.status] = this._reactInternalFiber.child.stateNode.childNodes["0"].childNodes["0"].childNodes["0"].childNodes["0"].childNodes["0"].childNodes["0"].checked;
    // this.painting();
    // this.setState(function(state) {
    //   return {
    //     radioGradient: obj
    //   };
    // });
  }
  
  render(){
   // console.log(this.state.quantity);
  const variable = this.props.func.state.valueElem[this.props.status];
    
  const statusButton = variable['radioGradient'] != undefined? variable['radioGradient']:[true,false,false],
        chek0 = statusButton[0],
        chek1 = statusButton[1],
        chek2 = statusButton[2];

  var leng = this.state.arryColor[this.props.status][2];
  const degGradients = variable['colorGradient'] != undefined? variable['colorGradient']['deg']:180;
  const colorGradients = variable['colorGradient'] != undefined? variable['colorGradient']['color']:['#ffffff', '#000000'];
  const rangeGradients = variable['colorGradient'] != undefined? variable['colorGradient']['range']:['0', '100'];
  var tery = this;
  var arColor = this.state.arryColor;
  var func = this.painting.bind(this);
  var degFunction = this.degFunc.bind(this);
   //console.log(this.megoArr);
  // this.addGradient()
   //console.log(this); 
      
      const nameLengths = colorGradients.map(function(name, index) {
      //  console.log(index);
        // function even_or_odd(number) {
        //       return number % 2;
        // }
       // var chet = even_or_odd(colorGradients.length);
       console.log(colorGradients);
         // console.log(name+' = '+index+' = '+leng-1);
          return(<li className="dinlineitem qty2 size-item" key={index+1}>
                    <h2>COLOR {index+1}</h2>
                    <input type="color" onClick={func} data-indexR={index} onChange={func} value={colorGradients[index]} /><br />
                    <input type="range" min="0" max="100" data-indexR={index} onChange={func} className="marginTop" value={rangeGradients[index]} />
                    <input type="text" className="inputTextStyle" onChange={func} data-indexR={index} value={rangeGradients[index]} />
                 </li>);
      });
     // var degInp = <input type="range" min="0" max="360" onChange={func} />
      // получили массив с длинами  <tbody ref={this.grad}>
     // console.log( nameLengths ); // 4,3,10onChange={this.addGradient.bind(this)}this.sizeIn = React.createRef();
    const deged = !chek1?<div><span>Angle</span><br /><input type="range" data-indexR={'deg'} min="0" max="360" onChange={degFunction} value={degGradients} /><input type="text" className="inputTextStyle" onChange={degFunction} value={degGradients} /></div>:'';
    const classN1 = statusButton[0]? 'menu-r-angle-Check miniS-Font':'menu-r-angle miniS-Font',
          classN2 = statusButton[1]? 'menu-r-angle-Check miniS-Font':'menu-r-angle miniS-Font',
          classN3 = statusButton[2]? 'menu-r-angle-Check miniS-Font':'menu-r-angle miniS-Font';
        return(
          <div>
            <div className="dtfull size-item">
              <div className="dinlineitem qty">
                <label className={classN1}><input type="radio" className="checSection" data-index={'0'} name="radioGradient" onChange={this.gradient.bind(this)} checked={chek0} />LINEAR</label>
              </div>
              <div className="dinlineitem qty">
                <label className={classN2}><input type="radio" className="checSection" data-index={'1'} name="radioGradient" onChange={this.gradient.bind(this)} checked={chek1} />RADIAL</label>
              </div>
              <div className="dinlineitem qty">
                <label className={classN3}><input type="radio" className="checSection" data-index={'2'} name="radioGradient" onChange={this.gradient.bind(this)} checked={chek2} />REPEATING-LINEAR</label>
              </div>
            </div>
            {deged}
            <ul className="dtfull size-item pad-lr-0">
                {nameLengths}
            </ul>
            <button onClick={this.add.bind(this)}>добавить</button>
            <button onClick={this.dell.bind(this)}>удалить</button>
          </div>
        ); 
  }
}

class Url extends Component {
//this.props.func.funcThis('background', 'radial-gradient('+newArray.join(', ')+')',this.props.status,'colorGradient', colorList['colorGradient']);
  urlFunc(e){
    console.log('karakcha');
    console.log(e.target.value);
    const url = 'url('+e.target.value+')'
    this.props.func.funcThis('background-image', url, this.props.status, 'background-image', url);
  }
  repeat(e){
    const repeat = e.target.getAttribute('data-e'),
          arr = [false,false,false,false];
          arr[e.target.getAttribute('data-h')] = true;
    this.props.func.funcThis('background-repeat', repeat, this.props.status, 'repeatButton', arr);
  }
  position(e){
    const short = this.props.func.state.valueElem[this.props.status],
          arr = short.valBG!=undefined?short.valBG:[0,0,0,0];
          arr[e.target.getAttribute('data-p')] = e.target.value;
    const pos = arr[0]+'px '+arr[1]+'px'; 

    this.props.func.funcThis('background-position', pos, this.props.status, 'valBG', arr);
  }
  sizing(e){
    const short = this.props.func.state.valueElem[this.props.status],
          arr = short.valBG!=undefined?short.valBG:[0,0,0,0];
          arr[e.target.getAttribute('data-p')] = e.target.value;
    const siz = arr[2]+'px '+arr[3]+'px'; 

    this.props.func.funcThis('background-size', siz, this.props.status, 'valBG', arr);
  }
  render(){
    //80%
    const short = this.props.func.state.valueElem[this.props.status];
    const repeatArr = short.repeatButton!=undefined?short.repeatButton:[false,true,false,false],
          class0 = repeatArr[0]?'statusCheck':'status',
          class1 = repeatArr[1]?'statusCheck':'status',
          class2 = repeatArr[2]?'statusCheck':'status',
          class3 = repeatArr[3]?'statusCheck':'status';
   const val = short.valBG!=undefined?short.valBG:[0,0,0,0];

    return (<div className="size-item">
      <span>URL: </span><input type="url" className="url-img" onChange={this.urlFunc.bind(this)} />
      <div className="block-repeat">
        <label className={class0}><input className="checSection" data-e="no-repeat" data-h="0" onChange={this.repeat.bind(this)} type="radio" name="repeat-bg-url" checked={repeatArr[0]} />NO-REPEAT</label>
        <label className={class1}><input className="checSection" data-e="repeat" data-h="1" onChange={this.repeat.bind(this)} type="radio" name="repeat-bg-url" checked={repeatArr[1]} />REPEAT</label>
        <label className={class2}><input className="checSection" data-e="repeat-x" data-h="2" onChange={this.repeat.bind(this)} type="radio" name="repeat-bg-url" checked={repeatArr[2]} />REPEAT-X</label>
        <label className={class3}><input className="checSection" data-e="repeat-y" data-h="3" onChange={this.repeat.bind(this)} type="radio" name="repeat-bg-url" checked={repeatArr[3]} />REPEAT-Y</label>
      </div>
      <h3>BACKGROUND-POSITION</h3>
      <article className="dtfull">
        <div className="t-td">
          <h2>position-x</h2>
          <input type="range" data-p="0" onChange={this.position.bind(this)} value={val[0]} /><input type="text" data-p="0" onChange={this.position.bind(this)} className="inputTextStyle" value={val[0]} />
        </div>
        <div className="t-td">
          <h2>position-y</h2>
          <input type="range" data-p="1" onChange={this.position.bind(this)} value={val[1]} /><input type="text" data-p="1" onChange={this.position.bind(this)} className="inputTextStyle" value={val[1]} />
        </div>
      </article>
      <h3>BACKGROUND-SIZE</h3>
      <article className="dtfull">
        <div className="t-td">
          <h2>size-width</h2>
          <input type="range" data-p="2" onChange={this.sizing.bind(this)} value={val[2]} /><input type="text" data-p="2" onChange={this.sizing.bind(this)} className="inputTextStyle" value={val[2]} />
        </div>
        <div className="t-td">
          <h2>size-height</h2>
          <input type="range" data-p="3" onChange={this.sizing.bind(this)} value={val[3]} /><input type="text" data-p="3" onChange={this.sizing.bind(this)} className="inputTextStyle" value={val[3]} />
        </div>
      </article>
    </div>);
  }
}


