import React, { Component } from 'react';
import BigBlock from './bigBlock';
import './brReact.css';

class CodPan extends Component {

	constructor(props){
	    super(props);
	    this.state = {dor: false};
  	}
	opens(e){
		console.log(e.target.checked);
		this.setState({dor:e.target.checked});
	}

	render() {
		//document.documentElement.clientWidth
		const blockCode = this.state.dor? <CodBlock status={this.props.status} tun={this.props.tun} />:null,
			  class1 = this.state.dor?'statusBarCheck':'statusBar';
			  console.log(document.documentElement.clientWidth);
		return(
			<article>
				<label className={class1}><input type="checkbox" className="checSection" onChange={this.opens.bind(this)} />CSS CODE</label>
				{blockCode}
			</article>
		)
	}
}
export default CodPan;

class CodBlock extends Component {

	changer(e){
		const text = e.target.value.replace(/[\n\t](\t\s)/g, ''); //.split(';');
		
		const text2 = text.split(/;/);
		console.log(text2);
		const keys = text2.map(function(name, index) {
			return text2[index].split( /:\s/); 
		});

		console.log(keys);
		this.props.tun.funcThis('area', keys);

	}
	render() {
		//console.log(this.props.tun.state.style[this.props.status]);
		const way = this.props.tun.state.style[this.props.status];
		console.log(way);
		const code = Object.keys(way).map(function(name){
			return '\t'+name+': '+way[name]+';';
		});
		// .map(function(key, index) {
		// 	return (<div>{index}</div>);
	 //    });
	 //   console.log(code);
	    const text = code.join('\n');
	    //value={code}
		return(
			<article>
				<textarea className="codPan" value={text} onChange={this.changer.bind(this)}>
					
				</textarea>
			</article>
		)
	}
}


