import React, { Component } from 'react';
import './brReact.css';
import State from './state';
import SizeButton from './sizeButton';
import CodPen from './codPan';
import Boxshadow from './boxshadow';
// var blockStandart = document.getElementById('wh1skasGeneratorStandart');
// var aryShadow = ['0 0 0 0 black', '0 0 0 0 black'];
//
// var celectStandartInput = document.getElementById('celectStandartInput'),
//     celectHoverInput = document.getElementById('celectHoverInput'),
//     celectActiveInput = document.getElementById('celectActiveInput');

// var firstBS = true,
// secondBS = true;

class BigBlock extends Component {

  constructor(props){
    super(props);
    this.state = {status:'s'+1, style:{'s1':[], 's2':[], 's3':[]}, valueElem:{'s1':{}, 's2':{}, 's3':{}}, dor:[]}
    this.buttest = React.createRef();
    this.buta = this;
    // this.item = {'s1':[], 's2':[], 's3':[]};
  }
  onHover(){
   // console.log('Навелд')
  }
  onOut(){
  //  console.log('Увелд')
  }
  status(i){
    //console.log(i);
    this.setState(function(state) {
      return {
        status: i
      };
    });

  }
  funcThis(styl, text, status, name, value){

  // console.log(this.state.status);
  console.log(styl+', '+text+', '+status+', '+name+', '+ value);
    // this.item[status][styl] = text;
    const arrStyle = this.state.style;
    const arrValue = this.state.valueElem;
    const support = {'s1':[], 's2':[], 's3':[]};
    if(styl === 'area'){
      console.log(text);
     // for(let i=0;i<text.length; i++){
      for(let i in text)  {
        console.log(text);
        if(text[i][0] != ''){
          support[this.state.status][text[i][0].replace(/\s/g, '')] = text[i][1]!=undefined?text[i][1].replace(/;/g, ''):'';
        }
      }
      arrStyle[this.state.status] = support[this.state.status];
      console.log(arrStyle);
    }else if(styl != 'none'){
      arrStyle[status][styl] = text;
      arrValue[status][name] = value;
    } else{
      arrValue[status][text] = name;
    }
    
      this.setState(function(state) {
        return {
          style:  arrStyle, valueElem: arrValue
        };
      });
     // console.log(this);
   // this.state.style[styl] = text;
  }
  // opened(index){
  //   const arrOpen = this.state.dor.length==0?[]:this.state.dor;
  //   for(let i=0;i<=2;i++){
  //     arrOpen[i] = false
  //   }
  //   arrOpen[index] = true;
  //   this.setState(function(state) {
  //       return {
  //         dor:  arrOpen
  //       };
  //     });
  // }
  render() {
  //  console.log(this)
   // console.log(this);
    // const listShadow = this.state.num.map((i) =>
    //   <Welcome key={i} index={i} rang={0} />) document.documentElement.clientWidth/Height
    // document.getElementById('windowShow').clientHeight/2
    // var scrolltop = document.getElementById('windowShow').clientHeight/2;
    console.log(this.state.dor);
    const stelBoxButton = {top: 250};
    // arrOpen = this.state.dor.length!=0?this.state.dor:[];
    // if(arrOpen.length===0){
    //   for(let i=0;i<=2;i++){
    //     arrOpen[i] = false
    //   }
    // }

    return (
      <div ref={this.buttest}>
        <div className="brReact" onMouseEnter={this.onHover.bind(this)} onMouseLeave={this.onOut.bind(this)}>
          <State onChange={this.status.bind(this)} rara={this.status.bind(this)} />
          <SizeButton status={this.state.status} elem={this.buta} tun={this} />
          <Boxshadow status={this.state.status} tun={this} />
          <CodPen status={this.state.status} tun={this} />
        </div>
        <div className="fix" style={stelBoxButton}>
          <div className="boxButton">
            <ButtonT key="bu" status={this.state.status} styler={this.state.style}>Кнопка</ButtonT>
          </div>
        </div>
      </div>
    );
  }
}

export default BigBlock;

class ButtonT extends Component {

  constructor(props){
    super(props);
   
  }




    render() {
     // console.log(this.props.styler);[this.props.status]
     console.log(this.props);
      var stel = {}
      //this.props.styler
      for (var prop in this.props.styler[this.props.status]) {
          stel[prop] = this.props.styler[this.props.status][prop];
       // console.log("obj." + prop + " = " + this.props.styler[prop]);
      }
     // console.log(stel);
      //var stel = {this.props.styler};
      // const element = <button className="buttonTest" >Кнопка</button>;
      // console.log(element);
      return(
        <button className="buttonTest" style={stel} >Кнопка</button>
      )
  }
}



